import { TextBackgroundPluginConfigs } from './types';
import createRenderNode from './renderNode';
import createRenderContent from './renderContent';

export function createTextBackgroundPlugin(configs: any) {
  const tbConfigs = configs?.textBackground as
    | TextBackgroundPluginConfigs
    | undefined;
  return {
    renderNode: createRenderNode(tbConfigs),
    renderEditable: createRenderContent(tbConfigs),
    models: [],
  };
}
